<style lang="scss">
.wx_loading {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.4);
    .ui_hide_loading{
		position: absolute;
		top: 0.2666666666rem;
		right: 0.2666666666rem;
		z-index: 99999;
		width: 0.55rem;
		height: 0.55rem;
		background-color: rgba(0, 0, 0, 0.5);
    	color: #ffffff;
    	line-height: 0.55rem;
    	border-radius: 50%;
    	text-align: center;
	}
}

.wx_loading .wx_loading_inner {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.5);
    color: #ffffff;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-left: -70px;
    margin-top: -48px;
    width: 150px;
    border-radius: 6px;
    font-size: 14px;
    padding: 58px 0 10px 0;
}

@keyframes gif {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes gif {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

.wx_loading .wx_loading_icon {
    position: absolute;
    top: 15px;
    left: 50%;
    margin-left: -16px;
    width: 24px;
    height: 24px;
    border: 2px solid #fff;
    border-radius: 24px;
    -webkit-animation: gif 1s infinite linear;
    animation: gif 1s infinite linear;
    clip: rect(0 auto 12px 0);
}

</style>
<template>
	<div>
	    <div v-show="myShowLoadView" class="wx_loading" >
	    	<div class="ui_hide_loading" @click="clickHide" style="display: none;">跳过</div>
	        <div class="wx_loading_inner">
	            <i class="wx_loading_icon"></i>{{showTxt}}
	        </div>
	    </div>
	</div>
</template>
<script>
export default {
    replace: true,
    props: ['showTxt', "showLoadView"],
    data(){
    	return {
            myShowLoadView: this.showLoadView
        }
    },
    methods:{
    	clickHide(){
    		var _this=this;
    		_this.myShowLoadView=false;
    	}
    },
    watch: {
        showLoadView(val) {
            this.myShowLoadView = val;
        }
    }
    
}
</script>



